<template>
  <div class="quiz-wrapper w-full lg:w-2/3 xl:w-3/5">
    <div class="quiz__header">
      <h2 class="gobold-high-bold text-center">{{ title }}</h2> 
    </div> 
    <div class="quiz__main py-12" v-for="(item, index) in items.slice(a,b)" :key="index" v-show="quiz">
      <div class="quiz__question paralucent-demibold text-2xl mb-8 pb-8 px-8 md:px-16">
        <h2 class="paralucent-demibold">{{ item.question }}</h2> 
      </div> 
      <div class="quiz__choices px-8 md:px-16">
        <ul>
          <li v-for="(choice, index) in item.choices" :key="index" class="choice w-full py-4 my-2 px-4 md:px-8 minionpro-regular text-xl" :class="select ? check(choice) : ''" @click="selectResponse(choice)">
            {{ choice.answer }}
          </li> 
        </ul> 
      </div> 
    </div> 
    <div class="quiz__score w-full flex py-24 flex-col items-center justify-center" v-if="showScore">
      <h2 class="paralucent-medium text-xl mb-3">Your score is</h2> 
      <h1 class="paralucent-bold text-6xl mb-12">{{score}}/{{items.length}}</h1> 
      <button @click="restartQuiz()" class="paralucent-light next score-buttons px-12 py-2 rounded-sm text-xl text-white mb-5">Restart</button> 
      <router-link to="/quizzes" class="paralucent-light skip score-buttons px-12 py-2 rounded-sm text-xl text-center ">Back to quizzes</router-link>
    </div> 
    <div class="quiz__footer px-8 md:px-16 flex flex-col items-center" v-show="quiz">
      <div class="flex flex-col sm:flex-row justify-around items-center w-full">
        <button @click="skipQuestion()" class="paralucent-light px-12 py-2 rounded-sm text-xl w-full sm:w-auto" :class="select ? 'skip' : 'next text-white'">Skip</button>
        <p class="minionpro-regular order-3 sm:order-2">{{b}}/{{items.length}}</p> 
        <button @click="nextQuestion()" class="order-2 sm:order-3 paralucent-light px-12 py-2 rounded-sm text-xl w-full sm:w-auto mt-4 mb-8 sm:mb-0 sm:mt-0" :class="!select ? 'skip' : 'next text-white'">Next</button> 
      </div> 
      <router-link to="/quizzes" class="mt-10 minionpro-regular underline cursor-pointer" style="color: rgb(138, 137, 136); ">Back to quizzes</router-link>
    </div> 
  </div>      
</template>

<script>
export default {
  name: 'Quiz', 
  data() {
    return {
      a: 0, 
      b: 1, 
      select: false, 
      score:0, 
      quiz: true, 
      showScore: false, 
    }
  }, 
  props: {
    title: {
      type: String,
      default: 'Title'
    }, 
    items: {
      type: Array, 
      default: null 
    }
  }, 
  methods: {

    selectResponse(e){
      this.select = true; 
      if (e.correct) {
        this.score++; 
      }
    },

    check(status){
      if(status.correct){
        return 'correct'
      }
      else {
        return 'incorrect'
      }
    }, 

    nextQuestion(){
      if(!this.select) {
        return; 
      }
      if(this.items.length - 1 == this.a){
        this.showScore = true; 
        this.quiz = false; 
      }
      else {
        this.a++; 
        this.b++; 
        this.select = false; 
      }
    }, 

    skipQuestion(){
      if(!this.select) {
        if(this.items.length - 1 == this.a){
          this.showScore = true; 
          this.quiz = false; 
        } else {
          this.a++; 
          this.b++; 
        }
      }
    }, 

    restartQuiz(){
      Object.assign(this.$data, this.$options.data()); 
    }

  }
}
</script>

<style scoped lang="scss">
.quiz-wrapper{
    min-height: 400px; 
    .quiz__header {
      h2 {
        font-size: 3.5rem; 
      }
    }
    .quiz__main {
      .quiz__question {
        border-bottom: 1px solid rgb(197, 197, 197); 
      }
      .choice:hover {
        background-color: #e6e5e4; 
        cursor: pointer; 
      }
      li.choice.correct {
        background-color: rgb(178, 223, 174); 
      }
      li.choice.incorrect {
        background-color: rgb(235, 196, 196); 
      }
    }

    .quiz__footer, .quiz__score {
      button, a {
        transition: background-color 0.5s ease; 
      }
      button.skip, a.skip {
        background-color: rgb(211, 209, 208); 
        color: rgb(138, 137, 136); 
      }
      button.skip:hover, a.skip:hover {
        cursor: default; 
      }
      button.next {
        background-color: rgb(54, 54, 54); 
      }
      button.next:hover {
        background-color: rgb(65, 65, 65); 
      }
      .score-buttons {
        width: 250px; 
      }
      .score-buttons.skip {
        color: #181818; 
      }
      .skip.score-buttons:hover {
        background-color: rgb(192, 190, 189); 
        cursor: pointer; 
      }
    }
  }
</style>